body {
  --blue: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo img {
  width: 40px;
  height: 40px;
  position: relative;
  top: 3px;
}

.nav-list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item:not(:first-child) {
  margin-left: 20px;
}

.nav-link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav-link.is-active {
  color: var(--blue);
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-link:hover {
  color: var(--blue);
}
